import {Component, ElementRef, EventEmitter, Input, Output, ViewChild} from '@angular/core';
import {Directionality} from '@angular/cdk/bidi';
import {transition, trigger, useAnimation} from '@angular/animations';
import {AdaptAnchorNavigatorModel} from '../anchor-navigator.model';
import {ANIMATION_DURATION, ANIMATION_NAME} from '../../common/animation';

@Component({
  selector: 'adapt-anchor-link',
  templateUrl: './anchor-link.component.html',
  animations: [
    trigger('anchorAppearance', [
      transition('void => true', [
        useAnimation(ANIMATION_NAME.moveIn, {
          params: {
            duration: ANIMATION_DURATION.medium,
            translateX: -44,
            unit: 'px'
          }
        })
      ]),
      transition('void => false', [
        useAnimation(ANIMATION_NAME.moveIn, {
          params: {
            duration: ANIMATION_DURATION.medium,
            translateX: 44,
            unit: 'px'
          }
        })
      ]),
      transition('true => void', [
        useAnimation(ANIMATION_NAME.moveOut, {
          params: {
            duration: ANIMATION_DURATION.medium,
            translateX: -44,
            unit: 'px'
          }
        })
      ]),
      transition('false => void', [
        useAnimation(ANIMATION_NAME.moveOut, {
          params: {
            duration: ANIMATION_DURATION.medium,
            translateX: 44,
            unit: 'px'
          }
        })
      ])
    ])
  ],
})
export class AdaptAnchorLinkComponent {
  @Input() item: AdaptAnchorNavigatorModel;
  @Input() isMobile: boolean = false;
  @Input() isNavigatorVisible: boolean = true;

  @ViewChild('anchor', {static: true}) anchor: ElementRef;
  @ViewChild('animationLine', {static: true}) animationLine: ElementRef;

  /* eslint-disable-next-line @angular-eslint/no-output-on-prefix */
  @Output() readonly onLinkClick: EventEmitter<AdaptAnchorNavigatorModel> = new EventEmitter<AdaptAnchorNavigatorModel>();

  isRtl: boolean = false;

  constructor(private _dir: Directionality) {
    this.isRtl = this._dir.value === 'rtl';
  }

  navigateTo(event: Event): void {
    event.stopPropagation();
    event.preventDefault();

    this.onLinkClick.emit(this.item);
  }
}
